___CSS_LOADER_EXPORT___.push([module.id, `.User-Interface-Containers.Main-Container {
  grid-column: 2/3;
  grid-row: 7/8;

  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;

  box-sizing: border-box;
}

.Search-Bar-Input.Weather-Location {
  width: 300px;
  height: 30px;

  padding: 3px;

  margin-bottom: 10px;

  border-radius: 10px;
  border: none;

  background-color: white;
  color: white;

  font-size: 1rem;
  font-weight: 500;

  transition: background-color 0.2s ease;
}

.Search-Bar-Input.Weather-Location:focus {
  outline: 1.5px solid white;

  background-color: rgba(0, 0, 0, 0.6);
  box-shadow: none;

  transition: background-color 0.2s ease;
}

.User-Interface-Containers.Button-Container {
  display: inherit;

  align-items: center;
}

.User-Interface-Buttons {
  margin-inline: 5px;

  height: 30px;

  font-weight: 600;

  border-radius: 10px;
  border: none;

  background-color: rgba(0, 0, 0, 0.75);
  color: white;

  transition: background-color 0.2s ease;
}

.User-Interface-Buttons:hover {
  cursor: pointer;

  outline: none;
  background-color: white;

  color: rgb(65, 65, 65);
}
