___CSS_LOADER_EXPORT___.push([module.id, `.Current-Weather.Main-Container {
  grid-column: 1/4;
  grid-row: 3/8;

  display: grid;

  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(7, 1fr);

  margin-left: 35px;
  margin-right: 60px;
}

.Current-Weather.Condition-Text {
  font-size: 3.2rem;
  font-weight: 500;

  margin-bottom: 35px;

  grid-column: 1/2;
  grid-row: 2/4;
}

.Current-Weather.Temp {
  font-size: 2.1rem;

  grid-column: 1/2;
  grid-row: 4/5;

  margin-left: 15px;
  margin-top: 10px;
}

.Current-Weather.Feels-Like-Temp {
  font-size: 0.7rem;

  grid-column: 1/2;
  grid-row: 5/6;

  margin-left: 15px;
}

.Current-Weather.Precip {
  font-size: 1.6rem;

  grid-column: 1/2;
  grid-row: 6/7;

  margin-left: 15px;
}

.Current-Weather.Humidity {
  justify-self: flex-end;
  align-self: flex-end;

  grid-column: 2/3;
  grid-row: 2/3;

  padding-bottom: 15px;

  font-size: 1.5rem;
}

.Current-Weather.Visibility {
  justify-self: flex-end;

  grid-column: 2/3;
  grid-row: 3/4;

  font-size: 1.1rem;
}

.Current-Weather.Wind-Direction {
  justify-self: flex-end;
  align-self: flex-end;

  grid-column: 2/3;
  grid-row: 4/5;

  margin-bottom: 5px;
  margin-right: 15px;

  font-size: 0.9rem;
}

.Current-Weather.Wind-Degree {
  justify-self: flex-end;
  align-self: center;

  grid-column: 2/3;
  grid-row: 5/6;

  margin-right: 15px;

  font-size: 0.9rem;
}

.Current-Weather.Wind-Speed {
  justify-self: flex-end;
  align-self: flex-start;

  grid-column: 2/3;
  grid-row: 6/7;

  margin-top: 5px;
  margin-right: 15px;

  font-size: 0.9rem;
}
